import { BaseControl, PanelBody } from '@wordpress/components'
import { defaultSettings, initialize } from '@wordpress/codeEditor'
import { getUniqueID, minifyCSS } from '@stackable/util'
import { __ } from '@wordpress/i18n'
import { Component } from '@wordpress/element'
import { i18n } from 'stackable'

/**
 * "Compiles" CSS - compiles the CSS for use of a specific block only.
 *
 * @param {string} css
 * @param {string} mainClass
 * @param {string} uniqueID
 *
 * @return {string} CSS
 */
const compileCSS = ( css, mainClass, uniqueID ) => {
	// Regex steps:
	// Add the unique ID:
	// 		".ugb-accordion" -> ".uniqueID .ugb-accordion"
	// 		".ugb-accordion__title" -> ".uniqueID .ugb-accordion__title"
	// Connect the unique ID and the main class:
	// 		".ugb-accordion" -> ".uniqueID.ugb-accordion"
	// 		".ugb-accordion__title" -> ".uniqueID .ugb-accordion__title"
	return css.replace( /\/\*[\s\S]*?\*\//g, '' )
		.replace( /\/\/(.*)?\n/g, '' )
		.replace( /([^}]+)({)/g, ( match, selector, paren ) => {
			// Support for media queries (and other types e.g. support query).
			if ( selector.match( /@\w+/g ) ) {
				return selector.replace( /(@\w+[^{]+{\s*)([^{]+)/g, ( match, mediaQuery, selector ) => {
					const newSelector = prependCSSClass( selector, mainClass, uniqueID )
					return `${ mediaQuery } ${ newSelector } ${ paren }`
				} )
			}

			const newSelector = prependCSSClass( selector, mainClass, uniqueID )
			return `${ newSelector } ${ paren }`
		} ).trim()
}

/**
 * Ensures the cssSelector is only applied to the uniqueClassName element.
 * Wraps the cssSelector with a uniqueClassName, and takes into account the mainClassName.
 *
 * For example:
 * .title-block -> .my-title-be8d9a.title-block
 * .title-block span -> .my-title-be8d9a.title-block span
 * span -> .my-title-be8d9a span
 *
 * @param {string} cssSelector The CSS selector.
 * @param {string} mainClassName The main class of the block to target.
 * @param {string} uniqueClassName The unique parent classname to wrap the selector.
 * @param {string} wrapSelector All selectors will be wrapped in this if provided.
 *
 * @return {string} The modified CSS selector.
 */
export const prependCSSClass = ( cssSelector, mainClassName = '', uniqueClassName = '', wrapSelector = '' ) => {
	return cssSelector.trim().replace( /[\n\s\t]+/g, ' ' )
		.split( ',' )
		.map( s => {
			let newSelector = ''
			if ( ! uniqueClassName || ! mainClassName ) {
				newSelector = s
			} else if ( uniqueClassName && ! mainClassName ) {
				newSelector = `.${ uniqueClassName } ${ s.trim() }`
			} else {
				newSelector = `.${ uniqueClassName } ${ s.trim() }`
					.replace( new RegExp( `(.${ uniqueClassName }) (.${ mainClassName }(#|:|\\[|\\.|\\s|$))`, 'g' ), '$1$2' )
			}
			return wrapSelector ? `${ wrapSelector } ${ newSelector }` : newSelector
		} )
		.join( ', ' )
}

class PanelCustomCSSSettings extends Component {
	constructor() {
		super( ...arguments )
		this.state = {
			opened: false,
		}
		this.onToggle = this.onToggle.bind( this )
		this.onChange = this.onChange.bind( this )
		this.codeMirror = null
	}

	onToggle() {
		this.setState( { opened: ! this.state.opened } )
	}

	throttledOnChange( customCSS ) {
		const { defaultCSS, mainClass, blockProps } = this.props

		// Create a unique ID based on the block's clientId.
		const customCSSUniqueID = getUniqueID( blockProps.clientId )

		// Parse the CSS.
		const newMinified = minifyCSS( compileCSS( customCSS, mainClass, customCSSUniqueID ), true )
		const defaultMinified = minifyCSS( compileCSS( defaultCSS, mainClass, customCSSUniqueID ), true )
		const isModified = defaultMinified !== newMinified

		this.props.onChangeCustomCSS( isModified ? customCSS : '' )
		this.props.onChangeCompiledCSS( isModified ? newMinified : '' )
		this.props.onChangeCSSUniqueID( isModified ? customCSSUniqueID : '' )
	}

	// Called when the contents are changed. Throttle the call to prevent stalls.
	onChange( editor ) {
		const customCSS = editor.getValue ? editor.getValue() : editor
		if ( this.onChangeTimeout ) {
			clearTimeout( this.onChangeTimeout )
		}
		this.onChangeTimeout = setTimeout( () => {
			this.throttledOnChange( customCSS )
		}, 500 )
	}

	// Initialize / destroy the editor upon close/open of the panel.
	// The panel removes all DOM when closed, so we need to re-init the editor.
	componentDidUpdate( prevProps, prevState ) {
		if ( this.state.opened === prevState.opened ) {
			return
		}

		// Destroy the editor if closing.
		if ( ! this.state.opened && this.codeMirror ) {
			this.codeMirror.codemirror.off( 'change', this.onChange )
			this.codeMirror = null
		}

		// Initialize the editor if opening.
		if ( this.state.opened ) {
			this.codeMirror = initialize( 'ugb-custom-css__textarea', {
				...defaultSettings,
				codemirror: {
					...defaultSettings.codemirror,
				},
			} )
			this.codeMirror.codemirror.on( 'change', this.onChange )
		}
	}

	componentWillUnmount() {
		if ( this.codeMirror ) {
			this.codeMirror.codemirror.off( 'change', this.onChange )
			this.codeMirror = null
		}
	}

	render() {
		const value = this.props.value || this.props.defaultCSS.trim()

		return (
			<PanelBody
				className="ugb-custom-css-panel"
				initialOpen={ false }
				title={ __( 'Custom CSS', i18n ) }
				onToggle={ this.onToggle }
			>
				<p className="ugb-custom-css-description">{ __( 'You can use this area to further customize your block. Any custom CSS added here will only affect this block.', i18n ) }</p>
				<BaseControl
					className="ugb-custom-css-control"
				>
					<textarea id="ugb-custom-css__textarea" value={ value } onChange={ this.onChange } />
				</BaseControl>
				{ this.props.children }
			</PanelBody>
		)
	}
}

PanelCustomCSSSettings.defaultProps = {
	value: '',
	defaultCSS: '',
	customCSSUniqueID: '',
	blockProps: { clientId: '' },
	mainClass: '',
	onChangeCompiledCSS: () => {},
	onChangeCustomCSS: () => {},
	onChangeCSSUniqueID: () => {},
}

export default PanelCustomCSSSettings

