import { PanelBody, SelectControl, ToggleControl } from '@wordpress/components'
import { __ } from '@wordpress/i18n'
import { i18n } from 'stackable'

function PanelEffectsSettings( props ) {
	const {
		hoverEffect,
		grayscaleEffect,
		onChangeHoverEffect,
		onChangeGrayscaleEffect,
	} = props

	return (
		<PanelBody
			initialOpen={ false }
			title={ __( 'Effects', i18n ) }
			{ ...props }
		>
			{ onChangeHoverEffect &&
				<SelectControl
					label={ __( 'Hover Effect', i18n ) }
					value={ hoverEffect }
					options={ [
						{ value: '', label: __( 'None', i18n ) },
						{ value: 'shadow', label: __( 'Shadow', i18n ) },
						{ value: 'lift', label: __( 'Lift', i18n ) },
						{ value: 'lift-shadow', label: __( 'Lift w/ shadow', i18n ) },
						{ value: 'lift-staggered', label: __( 'Staggered lift', i18n ) },
						{ value: 'lift-shadow-staggered', label: __( 'Staggered lift w/ shadow', i18n ) },
						{ value: 'scale', label: __( 'Scale', i18n ) },
						{ value: 'scale-shadow', label: __( 'Scale w/ shadow', i18n ) },
						{ value: 'lower', label: __( 'Lower', i18n ) },
					] }
					onChange={ onChangeHoverEffect }
				/>
			}
			{ onChangeGrayscaleEffect &&
				<ToggleControl
					label={ __( 'Grayscale Hover Effect', i18n ) }
					checked={ grayscaleEffect }
					onChange={ onChangeGrayscaleEffect }
				/>
			}
			{ props.children }
		</PanelBody>
	)
}

export default PanelEffectsSettings
